*,
*::before,
*::after {
  box-sizing: inherit; }

html {
  box-sizing: border-box; }

.content-box-component {
  box-sizing: content-box; }

body {
  font-family: 'ABC Favorit Extended';
  position: relative;
  height: 100%;
  margin: 0 auto;
  background-color: #000; }
  @media (max-width: 767px) {
    body.lock {
      overflow: hidden; } }

a {
  text-decoration: none;
  color: inherit; }

ul,
ol,
li {
  padding: 0;
  margin: 0; }

img {
  max-width: 100%;
  height: auto;
  vertical-align: top; }

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  overflow: hidden; }

.link {
  transition: all .3s ease; }

.link:hover {
  opacity: .7; }

.hidden {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden; }

.img-bg {
  position: relative; }

.img-bg img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  object-fit: cover; }

.container {
  min-width: 320px;
  max-width: 1390px;
  margin: 0 auto;
  padding: 0 24px; }

.header {
  position: relative;
  padding: 2rem 0;
  border-bottom: 1px solid #fff; }

.nav {
  color: #fff;
  padding-left: 87px; }
  @media (max-width: 1048px) {
    .nav {
      padding-left: 0; } }
  .nav__list {
    padding: 0;
    margin: 0;
    list-style: none;
    display: flex;
    gap: 36px; }
    @media (max-width: 1048px) {
      .nav__list {
        justify-content: center; } }
  .nav__item {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: 0.05em;
    text-transform: uppercase; }
  .nav__item:nth-child(2) {
    margin-right: auto; }
    @media (max-width: 1048px) {
      .nav__item:nth-child(2) {
        margin-right: 0; } }

@media (hover: hover) {
  .nav__item {
    cursor: pointer;
    transition: all .3s ease; }
  .nav__item:hover {
    opacity: .7; } }

.banner {
  display: none;
  position: relative; }
  @media (max-width: 1386px) {
    .banner {
      display: block; } }
  .banner__caption {
    position: relative;
    transform: translateY(-30px);
    display: inline-block;
    margin: 0;
    color: #fff;
    font-family: 'Inter', sans-serif;
    font-weight: 500;
    font-size: 34px;
    line-height: 24px;
    text-align: center;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    border-bottom: 1px solid #000;
    background-color: #000;
    padding: 0 30px; }
    @media (max-width: 1048px) {
      .banner__caption {
        transform: none; } }
    .banner__caption::after {
      content: "";
      display: block;
      position: absolute;
      top: 0;
      left: -4px;
      width: 35px;
      height: 30px;
      border: 1px solid #fff;
      border-top: none;
      border-left: none;
      border-bottom-right-radius: 15px; }
      @media (max-width: 1048px) {
        .banner__caption::after {
          display: none; } }
    .banner__caption::before {
      content: "";
      display: block;
      position: absolute;
      top: 0;
      right: -4px;
      width: 35px;
      height: 30px;
      border: 1px solid #fff;
      border-top: none;
      border-right: none;
      border-bottom-left-radius: 15px; }
      @media (max-width: 1048px) {
        .banner__caption::before {
          display: none; } }
    .banner__caption span {
      display: block;
      transform: translateY(-29px);
      padding: 20px 20px;
      padding-bottom: 0;
      background-color: #000;
      border-left: 1px solid #fff;
      border-right: 1px solid #fff;
      border-top: 1px solid #fff;
      border-top-right-radius: 15px;
      border-top-left-radius: 15px; }
      @media (max-width: 1048px) {
        .banner__caption span {
          border: none; } }
  .banner__content {
    position: relative;
    padding: 60px 56px;
    padding-right: 32px;
    width: 100%;
    height: 516px;
    border: 1px solid #D9D9D9;
    border-radius: 30px;
    background-image: url("../img/bg.png");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center; }
  .banner__wrapper {
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    color: #fff; }
  .banner__text {
    text-transform: uppercase; }
    .banner__text--small {
      margin: 0;
      font-size: 16px;
      line-height: 24px;
      color: rgba(255, 255, 255, 0.5); }
    .banner__text--large {
      font-weight: 350;
      font-size: 16px;
      line-height: 64px;
      letter-spacing: -0.02em;
      text-transform: uppercase; }
  .banner__heading {
    text-align: left;
    margin: 0;
    margin-top: 8px;
    font-weight: 350;
    font-size: 56px;
    line-height: 64px;
    letter-spacing: -0.02em;
    text-transform: uppercase; }
  .banner__button {
    margin-top: 32px;
    max-width: 195px;
    color: #000;
    font-weight: 700;
    background-color: #fff;
    padding: 18px 30px;
    border: 1px solid #FFFFFF;
    border-radius: 0px 20px 20px 32px; }
  .banner__controls {
    display: flex;
    align-items: center;
    position: absolute;
    z-index: 5;
    width: 162px;
    height: 42px;
    padding: 0 22px;
    background-color: #000;
    left: calc(50% - 81px);
    bottom: -20px;
    border: 0.5px solid #FFFFFF;
    border-radius: 30px; }
  .banner__center {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    top: -10px;
    left: calc(50% - 32px);
    height: 64px;
    width: 64px;
    border-radius: 50%;
    border: 0.5px solid #FFFFFF;
    background-color: #000; }
  .banner__stop {
    width: 17px;
    height: 17px;
    border: 2px dotted #fff;
    background-color: #000;
    cursor: pointer; }
  .banner__toggle {
    width: 14px;
    height: 14px;
    padding: 2px;
    border: 2px dotted #fff;
    background-color: #000;
    cursor: pointer;
    transform: rotate(45deg); }
    .banner__toggle--left {
      border-right: none;
      border-top: none; }
    .banner__toggle--right {
      border-left: none;
      border-bottom: none;
      margin-left: auto; }
  .banner__logo {
    float: right; }
    @media (max-width: 1048px) {
      .banner__logo {
        position: absolute;
        top: 20px;
        right: 20px; } }
  .banner__bar {
    position: absolute;
    display: flex;
    height: 58px;
    width: 375px;
    align-items: center;
    justify-content: center;
    gap: 82px;
    background-color: #000;
    border: 1px solid #D9D9D9; }
    @media (max-width: 1048px) {
      .banner__bar {
        width: 240px;
        gap: 22px; } }
    .banner__bar--top {
      top: -1px;
      right: -1px;
      border-radius: 0px 0px 0px 30px;
      border-top-color: #000;
      border-right: #000; }
      @media (max-width: 1048px) {
        .banner__bar--top {
          display: none; } }
    .banner__bar--bottom {
      bottom: -1px;
      right: -1px;
      border-radius: 30px 0px 0px 0px;
      border-bottom-color: #000;
      border-right-color: #000; }

.banner__masked,
.banner__border {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%; }

.banner__masked {
  z-index: -1; }

.banner__border {
  z-index: 1; }

.banner--large {
  display: block; }
  @media (max-width: 1386px) {
    .banner--large {
      display: none; } }
  .banner--large .banner__content {
    border: none;
    background-image: unset; }
  .banner--large .banner__button {
    z-index: 3; }
  .banner--large .banner__bar {
    z-index: 3;
    border: unset; }
    .banner--large .banner__bar--top {
      top: 0;
      right: 0; }
    .banner--large .banner__bar--bottom {
      bottom: 0;
      right: 0; }

@media (hover: hover) {
  .banner__icon {
    transition: all .3s ease; }
    .banner__icon:hover {
      opacity: .7; }
  .banner__button {
    cursor: pointer;
    transition: all .3s ease; }
    .banner__button:hover {
      opacity: .7; }
  .banner__toggle,
  .banner__stop {
    opacity: .7;
    cursor: pointer;
    transition: all .3s ease; }
    .banner__toggle:hover, .banner__toggle:hover,
    .banner__stop:hover,
    .banner__stop:hover {
      opacity: 1; } }

.main {
  flex: 1 1 auto;
  text-align: center; }

.footer {
